body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  background-color: #2f6aaa;
  justify-content: center;
  height: 50px;
  margin: -20px -30px 20px;
  display: flex;
}

.search-form input {
  border: 1px solid #fff;
  border-radius: 4px;
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #000;
  cursor: pointer;
  background-color: #d3d3d3;
  border: none;
  border-radius: 4px;
  margin-left: -4px;
  padding: 0 5px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px #0000001a;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  color: #333;
  justify-content: space-around;
  margin: 5px 0;
  font-size: 14px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 30px;
  font-size: 16px;
  display: block;
